import countryArr from "@/components/country.json";
export default {
  props: ["data"],
  data: function data() {
    return {
      countryJSON: null,
      countryArr: []
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.countryJSON = countryArr; //格式化国家

    Object.keys(countryArr).forEach(function (key) {
      Object.keys(countryArr[key]).forEach(function (key2) {
        _this.countryArr.push({
          label: countryArr[key][key2].nameEN,
          value: countryArr[key][key2].code2
        });
      });
    });
    this.countryArr.sort(function (a, b) {
      return a.label.charCodeAt(0) - b.label.charCodeAt(0);
    });
  },
  methods: {
    setAddress: function setAddress() {
      var _this2 = this;

      //保存地址
      if (!this.data.firstName) {
        this.$elementMessage("Please enter First Name", "error");
        return;
      }

      if (!this.data.lastName) {
        this.$elementMessage("Please enter Last Name", "error");
        return;
      }

      if (!this.data.address1) {
        this.$elementMessage("Please enter Address1", "error");
        return;
      }

      if (!this.data.country) {
        this.$elementMessage("Please select Country", "error");
        return;
      }

      if (!this.data.province) {
        this.$elementMessage("Please enter Province", "error");
        return;
      }

      if (!this.data.city) {
        this.$elementMessage("Please enter City", "error");
        return;
      }

      if (!this.data.street) {
        this.$elementMessage("Please enter Street", "error");
        return;
      }

      this.data.loading = true;
      var params = {
        firstName: this.data.firstName,
        lastName: this.data.lastName,
        address1: this.data.address1,
        address2: this.data.address2,
        country: this.data.country,
        province: this.data.province,
        city: this.data.city,
        street: this.data.street,
        company: this.data.company,
        phone: this.data.phone,
        postcode: this.data.postcode,
        isDefault: this.data.isDefault ? 1 : 2,
        addressId: this.data.addressId
      };
      console.log(params);
      var api = params.addressId ? 'api.Address.updateAddress' : 'api.Address.addAddress';
      this.$apiCall(api, params, function (r) {
        _this2.data.loading = false;

        if (r.ErrorCode == "9999") {
          _this2.$emit('setAdd', params.addressId);
        } else {
          _this2.$elementMessage(r.Message, "error");
        }
      });
    },
    closeDialog: function closeDialog() {
      this.data.isShow = false;
    }
  }
};